import React from 'react'
// import SelectPage from '../../scripts/SelectPage'
import './index.css'
import ProdCarousel from '../ProdCarousel'
import $ from 'jquery'
// import triangle from '../../images/triangle.png'


export default class GoodsComponent extends React.Component {
    componentDidMount(){
        $('.spClass').addClass('arrowed')
        $('#region').addClass('arrowed')
    }
    
    render() {
        return (
            <div className='goodsWindow' id="sp">
                <div className="goodsContainer">
                    <div className="spDescription">
                        <div className="spInfo">
                            <h4 className="header smaller lined">-</h4>
                            <p className="text-data"> !       - . ,        !</p>
                            <div className="formSearch">
                                <form>
                                    <p>
                                    <label htmlFor="region" className="light"> :</label>
                                    <select id="region" name="region">
                                        <option></option>
                                        <option></option>
                                        <option></option>
                                    </select>
                                    <label htmlFor=""></label>
                                    <select className="spClass">
                                        <option>1  = 1 </option>
                                        <option>2  = 1 </option>
                                        <option>1  = 2 </option>
                                        <option>2  = 2 </option>
                                    </select>
                                    </p>

                                </form>
                            </div>
                        </div>
                    </div>
                    <div className="spCarousel">
                        <ProdCarousel />
                    </div>
                </div>
                <div className="wavingBg">
                </div>
            </div>
        )
    }
}